/* todo container */
.todo-container {
    min-width: 40%;
    height: auto;
    max-height: 600px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

/* form */
.todo-container form {
    width: 100%;
}

/* label */
.todo-container label {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    width: 100%;
    height: 40px;
}

/* input field */
.todo-container input {
    width: 90%;
    height: 45px;
    background-color: rgba(0, 0, 0, 0.2);
    border: none;
    outline: none;
    border-radius: 4px 0px 0px 4px;
    padding-left: 10px;
    color: #EEE;
}

/* input placeholder */
.todo-container input::placeholder {
    color: #EEE;
}

/* add button */
.todo-container button {
    min-width: 10%;
    height: 47px;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 0px 4px 4px 0px;
    border: 1px solid #EEE;
}

/* media queries */
@media (max-width: 850px) {
    .todo-container {
        min-width: 70%;
    }
}

@media (max-width: 576px) {
    .todo-container {
        min-width: 80%;
    }
}

@media (max-width: 400px) {
    .todo-container {
        min-width: 90%;
    }
}